import styled from "styled-components"
import { Container } from "components/layout"
import { SharedImage } from "constants/image-constants";
import JettImage from 'components/image';
import { useState } from "react";
import Lightbox from 'react-image-lightbox';

const { HEXADO_ARCHIVE_1, HEXADO_ARCHIVE_2, HEXADO_ARCHIVE_6, HEXADO_ARCHIVE_7, HEXADO_ARCHIVE_8, HEXADO_ARCHIVE_9 } = SharedImage;

const archiveImages = [
    HEXADO_ARCHIVE_1,
    HEXADO_ARCHIVE_2,
    HEXADO_ARCHIVE_6,
    HEXADO_ARCHIVE_7,
    HEXADO_ARCHIVE_8,
    HEXADO_ARCHIVE_9
]

export default function ArchivePage() {

    const [isOpen, setOpen] = useState(false);
    const [photoIndex, setPhotoIndex] = useState(0);

    const showImage = (index) => {
        setOpen(true);
        setPhotoIndex(index);
    }

    return (
        <Wrapper>
            <Container>
                <ArchiveGallery>
                    <JettImage src={HEXADO_ARCHIVE_1} className="hexado-archive" onClick={() => showImage(0)} />
                    <JettImage src={HEXADO_ARCHIVE_2} className="hexado-archive" onClick={() => showImage(1)} />
                    <JettImage src={HEXADO_ARCHIVE_6} className="hexado-archive" onClick={() => showImage(2)} />
                    <JettImage src={HEXADO_ARCHIVE_7} className="hexado-archive" onClick={() => showImage(3)} />
                    <JettImage src={HEXADO_ARCHIVE_8} className="hexado-archive" onClick={() => showImage(4)} />
                    <JettImage src={HEXADO_ARCHIVE_9} className="hexado-archive" onClick={() => showImage(5)} />
                </ArchiveGallery>
            </Container>
            {isOpen && (
                <Lightbox
                    mainSrc={archiveImages[photoIndex]}
                    nextSrc={archiveImages[(photoIndex + 1) % archiveImages.length]}
                    prevSrc={archiveImages[(photoIndex + archiveImages.length - 1) % archiveImages.length]}
                    onImageLoad={() => {
                        window.dispatchEvent(new Event('resize'));
                    }}
                    onCloseRequest={() => setOpen(false)}
                    onMovePrevRequest={() =>
                        setPhotoIndex((photoIndex + archiveImages.length - 1) % archiveImages.length)
                    }
                    onMoveNextRequest={() =>
                        setPhotoIndex((photoIndex + 1) % archiveImages.length)
                    }
                />
            )}
        </Wrapper>
    )
}

const Wrapper = styled.section`
    display: flex;
    flex-direction: row;
    justify-content: center;
    background-color: #03031B;
    padding: 80px 20px;
    min-height: 100vh;
`

const ArchiveGallery = styled.div`
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    grid-template-rows: repeat(8, 5vw);
    grid-gap: 15px;
    .hexado-archive {
        overflow: hidden;
        cursor: pointer;
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;    
        }
        &:nth-child(1) {
            grid-column-start: 1;
            grid-column-end: 3;
            grid-row-start: 1;
            grid-row-end: 3;
        }
        &:nth-child(2) {
            grid-column-start: 3;
            grid-column-end: 5;
            grid-row-start: 1;
            grid-row-end: 3;
        }
        &:nth-child(3) {
            grid-column-start: 5;
            grid-column-end: 9;
            grid-row-start: 1;
            grid-row-end: 6;
        }
        &:nth-child(4) {
            grid-column-start: 1;
            grid-column-end: 5;
            grid-row-start: 3;
            grid-row-end: 6;
        }
        &:nth-child(5) {
            grid-column-start: 1;
            grid-column-end: 5;
            grid-row-start: 6;
            grid-row-end: 9;
        }
        &:nth-child(6) {
            grid-column-start: 5;
            grid-column-end: 9;
            grid-row-start: 6;
            grid-row-end: 9;
        }
    }
`